import { Button, FormControl, FormLabel, Input, Modal, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay, Text, useDisclosure } from '@chakra-ui/react'
import axios from 'axios';
import React, { useEffect, useState } from 'react'

export const MessageFrequency = () => {
    const { isOpen, onOpen, onClose } = useDisclosure();
    const { isOpen: isTimeOpen, onOpen: onTimeOpen, onClose: onTimeClose } = useDisclosure();
    const [currentMessageFrequency, setcurrentMessageFrequency] = useState();
    const [changedTime, setchangedTime] = useState();
    const [weatherApiKey, setweatherApiKey] = useState('');


    const getCurrentMessageFrequency = async () => {
        try {
            const timeInSeconds = await axios('settings/getTime');
            setcurrentMessageFrequency(timeInSeconds.data.timeInSeconds);
        } catch (error) {
            console.log(error);
            throw new Error('Error')
        }
    }

    const changeTime = async () => {
        try {

            await axios.put('settings/change-time', { seconds: Number(changedTime) },
                {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                });
            getCurrentMessageFrequency();
            setchangedTime();
        } catch (error) {
            console.log(error);
        }

    }


    const handleApiKeys = async () => {
        await axios.post('settings/change-keys', { weatherApiKey: weatherApiKey },
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            });

        setweatherApiKey('');
    }



    useEffect(() => {
        getCurrentMessageFrequency();
    }, [])

    const isTimeInputValid = parseInt(changedTime) > 59 && parseInt(changedTime) < 2000001;
    const isApiInputValid = (weatherApiKey.length > 2 && weatherApiKey.length < 200)
    return (
        <div>
            <Text textAlign={'center'} as={'i'} fontSize='3xl'>
                {'Message Frequency: '}
            </Text>
            <Text textAlign={'center'} as={'i'} fontSize='3xl'>
                {currentMessageFrequency}{'(seconds)'}
            </Text>
            <div textAlign='center'>

                <Button colorScheme='green' mt={4} size={'lg'} onClick={onTimeOpen} >Change Time</Button>
                <Button colorScheme='green' mt={4} ml={2} size={'lg'} onClick={onOpen}>Change API Key</Button>
            </div>


            {/* api modal */}
            <Modal
                isOpen={isOpen}
                onClose={() => {
                    onClose();
                    setweatherApiKey('');
                }}
            >
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>Enter API key</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody pb={6}>

                        <FormControl mt={1}>

                            <FormLabel mt={4}>Open-Weather API Key <a target='_blank' rel="noopener noreferrer" href='https://openweathermap.org/'>
                                <Text as={'i'} color={'blue'}>openweathermap.org</Text>
                            </a>
                            </FormLabel>
                            <Input placeholder='required' isRequired='yes' autoComplete='off' onChange={(e) => setweatherApiKey(e.target.value)} />
                        </FormControl>
                    </ModalBody>

                    <ModalFooter>
                        <Button isDisabled={!isApiInputValid} colorScheme='blue' mr={3} onClick={() => {
                            onClose();
                            handleApiKeys();
                            window.location.reload();
                        }}>
                            Save
                        </Button>
                        <Button onClick={() => {
                            onClose();
                            setweatherApiKey('');
                        }}>Cancel</Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>

            {/* messageFrequency modal */}
            <Modal
                // initialFocusRef={initialRef}
                // finalFocusRef={finalRef}
                isOpen={isTimeOpen}
                onClose={() => {
                    onTimeClose();
                    setchangedTime();
                }}
            >
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>Change Message Frequency</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody pb={6}>
                        <FormControl>
                            <FormLabel>Enter time</FormLabel>
                            <Input type='number' onChange={(e) => setchangedTime(e.target.value)} placeholder='Enter time in seconds.(Min: 60, Max: 200000)' isRequired='yes' autoComplete='off' />
                        </FormControl>

                    </ModalBody>

                    <ModalFooter>
                        <Button isDisabled={!isTimeInputValid} onClick={() => {
                            // console.log('clicked')
                            changeTime();
                            onTimeClose();
                        }} colorScheme='blue' mr={3}>
                            Save
                        </Button>
                        <Button onClick={() => {
                            onTimeClose();
                            setchangedTime();
                        }}>Cancel</Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </div>
    )
}

export default MessageFrequency;