import React from 'react'
import {
    ListItem,
    UnorderedList,
    Text,
} from '@chakra-ui/react';

const UserInstructions = () => {
    return (
        <div>
            <Text display='grid' textAlign={'center'} fontSize='4xl' as={'b'} >Instructions</Text>
            <UnorderedList ml={50} mt={3} mb={3} >
                <ListItem>Click on user to get the user information.</ListItem>
                <ListItem>Change message frequency. {'(enter time in seconds)'}</ListItem>
                <ListItem>Get Open-weather-api <a href='https://openweathermap.org/' target='_blank' rel="noopener noreferrer"><Text as={'i'} color={'blue'} >openweathermap.org</Text></a></ListItem>
                <ListItem>Warning: Adding wrong API key may lead to stop the bot.</ListItem>
                <ListItem>Source Code: <a href='https://github.com/yourHighness11/node-telegram-bot' target='_blank' rel="noopener noreferrer"><Text as={'i'} color={'blue'} >Open</Text></a></ListItem>
                <ListItem>Have Fun! Enjoy</ListItem>
            </UnorderedList>
        </div>
    )
}

export default UserInstructions