import React, { useEffect, useState } from 'react'
import UserList from './UserList';
import { Container, Grid, GridItem, Text } from '@chakra-ui/react';
import UserInfo from './UserInfo';
import MessageFrequency from './MessageFrequency';
import axios from 'axios';
import UserInstructions from './UserInstructions';

const Home = () => {
    const [usersData, setusersData] = useState([])
    const getAllUsers = async () => {
        const allUsers = await axios.get('http://localhost:4000/users/')
        setusersData(allUsers.data);
    }
    useEffect(() => {
        getAllUsers()
    }, [])


    const [selectedUser, setSelectedUser] = useState(null);

    // Function to handle user click event
    const handleUserClick = (user) => {
        setSelectedUser(user);
    };
    return (


        <div>
            <Container maxW='100%' mb={2} bg={'#474350'} textAlign={'center'}>
                <Text textAlign={'center'} as={'b'} color={'white'} fontSize='4xl'>
                    TELEGRAM-BOT-ADMIN-PANEL

                </Text>
            </Container>
            <Grid
                h='550px'
                templateRows='repeat(2, 1fr)'
                templateColumns='repeat(5, 1fr)'
                gap={4}
            >
                {/* <GridItem rowSpan={2} colSpan={1} bg='tomato' /> */}
                <GridItem rowSpan={2} colSpan={1} bg='#F8FFF4' textAlign={'center'}>
                    <UserList users={usersData} onUserClick={handleUserClick} />
                </GridItem>
                <GridItem pb={4} colSpan={2} bg='#FAFAC6'>{selectedUser && <UserInfo user={selectedUser} />}</GridItem>
                <GridItem pb={4} colSpan={2} bg='#FCFFEB' textAlign={'center'}><MessageFrequency /></GridItem>
                <GridItem colSpan={4} bg='#FECDAA' ><UserInstructions /></GridItem>

            </Grid>
        </div>


    )
}

export default Home;