import { Button, ListItem, Text, UnorderedList, useDisclosure } from "@chakra-ui/react";
import {
    Modal,
    ModalOverlay,
    ModalContent,
    ModalHeader,
    ModalFooter,
    ModalBody,
    ModalCloseButton,
} from '@chakra-ui/react'
import axios from "axios";
import { useEffect, useState } from "react";
const UserInfo = ({ user }) => {
    const { isOpen, onOpen, onClose } = useDisclosure();

    const [isBlocked, setIsBlocked] = useState(false);

    const fetchUserData = async () => {
        try {
            const response = await axios.get(`users/${user.id}`); // Replace 'your-api-endpoint' with your actual API endpoint
            setIsBlocked(response.data.isBlocked); // Update isBlocked state with the fetched value
        } catch (error) {
            console.error('Error fetching user data:', error);
        }
    };

    useEffect(() => {
        fetchUserData();
    }, [user.id]);


    const toggleBlockedStatus = async () => {
        try {
            await axios.put(`users/block-or-unblock/${user.id}`);
            setIsBlocked(prevIsBlocked => !prevIsBlocked);
        } catch (error) {
            console.error('Error toggling blocked status:', error);
        }
    };

    const handleUserDeletion = async () => {
        await axios.delete(`users/delete/${user.id}`);
    }

    return (
        <div>
            <Text display='grid' textAlign={'center'} fontSize='4xl' as={'b'} >User Information</Text>
            <UnorderedList ml={'33%'} mt={5}>
                <ListItem fontSize='3xl'>Id: {user.id}</ListItem>
                <ListItem fontSize='3xl'>Name: {user.name}</ListItem>
                <ListItem fontSize='3xl'>City: {user.city}</ListItem>
                <ListItem fontSize='3xl'>Country: {user.country}</ListItem>

            </UnorderedList>
            <div style={{ textAlign: 'center' }}>
                <Button colorScheme='orange' mt={5} size={'lg'} onClick={() => {
                    toggleBlockedStatus();
                }}>{isBlocked ? 'Unblock User' : 'Block User'}</Button>
                <Button colorScheme='red' mt={5} ml={2} size={'lg'} onClick={onOpen}>Delete User</Button>

            </div>
            <Modal isOpen={isOpen} onClose={onClose}>
                <ModalOverlay />
                <ModalContent>
                    <ModalHeader>Confirmation!</ModalHeader>
                    <ModalCloseButton />
                    <ModalBody>
                        Do you want to delete the user.
                    </ModalBody>

                    <ModalFooter>
                        <Button colorScheme='blue' mr={2} onClick={() => {
                            handleUserDeletion();
                            onClose();
                            window.location.reload();
                        }}>Yes</Button>
                        <Button onClick={onClose}>
                            No
                        </Button>
                    </ModalFooter>
                </ModalContent>
            </Modal>

        </div>
    );
};

export default UserInfo;