import { Box, Button, Text } from '@chakra-ui/react'
const UserList = ({ users, onUserClick }) => {
    return (
        <div>

            <Text textAlign={'center'} fontSize='4xl' as={'b'}>Users</Text>

            <ul>

                {users.map(user => (
                    <div key={user._id}>
                        <Button colorScheme='teal' variant='outline' w={'90%'} mb={2} size={'lg'} key={user.id} onClick={() => onUserClick(user)}>
                            {user.name}
                        </Button>
                        {/* <Box bg='tomato' border='1px' borderColor='gray.200' w='100%' p={4} color='white' key={user.id} onClick={() => onUserClick(user)}>
                            {user.name}
                        </Box> */}
                    </div>

                ))}
            </ul>
        </div>
    );
};

export default UserList; 